/* Everything below is for ImageGallery.js */

* {
  box-sizing: border-box;
}

img {
  width: 100%;
  border: 5px solid theme("colors.shadowesque");
}

/* Used to center the image gallery on the home page */
.page-container {
  @apply flex flex-col justify-center bg-platinum;
}

/* Position the image container (needed to position the left and right arrows) */
.container {
  position: relative;
  
}

/* Add a pointer when hovering over the thumbnail images */
.cursor {
  cursor: pointer;
}

/* Next & previous buttons */
.prev,
.next {
  background-color: theme("colors.platinum");
  cursor: pointer;
  position: absolute;
  top: 40%;
  width: auto;
  padding: 16px;
  margin-top: -50px;
  color: theme("colors.burgundyesque");
  font-weight: bold;
  font-size: 20px;
  border-radius: 0 3px 3px 0;
  user-select: none;
  -webkit-user-select: none;
}

.prev {
  border-left: 5px solid theme("colors.shadowesque");
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
  border-right: 5px solid theme("colors.shadowesque");
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  background-color: theme("colors.secondary");
  color: theme("colors.primary");
}

/* Number text (1/6 etc) */
.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
  background: theme("colors.shadowesque");
  border-left: 5px solid theme("colors.shadowesque");
  border-top: 5px solid theme("colors.shadowesque");
}

/* Container for image text */
.caption-container {
  text-align: center;;
  padding: 2px 16px;
  color: white;
}

#caption {
  color: theme("colors.shadowesque");
  font-weight: bold;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Six columns side by side */
.column {
  float: left;
  width: 16.66%;
}

/* Add a transparency effect for thumnbail images */
.demo {
  opacity: 0.6;
}

.active,
.demo:hover {
  opacity: 1;
}